import React, { useState, useEffect, useCallback } from 'react';
import './SentenceGame2.css';

function SentenceGame2({ returnToSettings }) {
  const [sentences, setSentences] = useState([]);
  const [selectedSentences, setSelectedSentences] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [showOverlay, setShowOverlay] = useState(false);
  const [correctAnswer, setCorrectAnswer] = useState('');
  const [numSentences, setNumSentences] = useState(10);
  const [isConfigured, setIsConfigured] = useState(false);
  const [incorrectSentences, setIncorrectSentences] = useState([]);
  const [correctCount, setCorrectCount] = useState(0);
  const [options, setOptions] = useState([]);

  useEffect(() => {
    // 여기에 하드코딩된 데이터를 추가하세요
    const sentenceDict = {
      "The experiment showed that photos are ____ to videos in engaging viewer attention.": "superior",
      "Humans should live in ____ with nature. It is healthier for them and the environment.": "harmony",
      "As global trade and exploration increased, the need to transport food easily and safely became ____.": "critical",
      "Many people think that real plants are more attractive than ____ ones.": "fake",
      "Workers ____ hundreds of parts in a factory to make a car.": "assemble",
      "Then it ____ the letters as words and the words as sentences.": "assembles",
      "“He was terrified that I was leaving this stable job,” but Gutiérrez was ____ to ____ his idea.": "determined / pursue",
      "Not at all. He says as a child, he was very shy and his family had to push him to be more ____.": "assertive",
      "From the moment an animal is killed or a vegetable is picked, ____ begins to ____.": "bacteria / break it down",
      "Returning days or weeks later, they found that the milk had ____ into yogurt.": "transformed",
      "Even worse, because all his farm business was done in cash, he had no records of his income, and without records, he could not get a loan or ____ .": "insurance",
      "A long electrical ____ connects a table lamp to an outlet in a wall.": "wire",
      "This is because many businesses are moving away from traditional ____ models of selling products.": "ownership",
      "That’s a problem because researchers warn that ____ levels of fishing are not sustainable and that the global ____ of seafood may run out by then.": "current / supply",
      "The food was probably not very tasty, but it was ____ and kept the sailors alive, allowing global exploration to continue.": "nutritious",
      "You can ____ most recipes to feed more or fewer people.": "adapt",
      "In some factories, workers wear gloves to protect their hands from ____ chemicals.": "harmful",
      "Recent research has ____ for how we use our phones.": "implications",
      "He put food in glass jars, closed them ____, and then heated them to a high temperature.": "tightly",
      "Texting is not always the best ____ of communication.": "mode",
      "It is useful to have a ____ of colleagues who can give you career advice.": "network",
      "There was an almost unlimited supply of plastic, and it could be made with specific characteristics — hard or soft, ____ or ____, clear or colored.": "stiff / flexible",
      "____ of famous people usually ____ the strongest responses from the public.": "Portraits / prompt",
      "With a ____ ability to do things differently, today’s entrepreneurs are ____ markets from finance to technology.": "remarkable / disrupting",
      "Digital innovation can ____ traditional businesses. For example, more people now use ride-hailing companies like Uber than traditional taxis.": "disrupt",
      "A lot of people ____ that they recycle, but the evidence is that they do not.": "claim",
      "A picture can be a more effective ____ of communication than words to tell a story.": "mode",
      "Many of them don’t want the responsibility of having a lot of ____.": "possessions",
      "Some people have so many ____ that they cannot fit all of them into their homes.": "possessions",
      "We are hoping that the ____ situation will not last very long.": "current",
      "Each time a product comes back from a renter, it is refurbished, ____ the life of the product.": "extending",
      "Some studies focus ____ on children; no adults are included.": "exclusively",
      "Manufacturers throw away products that have ____ because consumers won't buy products that aren't perfect.": "flaws",
      "One ____ of economics is that consumers buy less when prices go up.": "principle",
      "We cannot ____ all our problems, but we can reduce the number of them.": "eliminate",
      "Many of the ordinary things that we use every day, from combs to toothbrushes, were once ____ items.": "luxury",
      "In the 1950s, plastic production ____.": "took off",
      "With another scientist, Ka Yi Ling, and the support of ____ and the Singapore government, she founded a company, Shiok Meats, in 2018.": "investors",
      "Many people ____ their families first when they need financial help.": "turn to",
      "____ items should be taxed more than everyday items like food and clothes.": "Luxury",
      "Before its release in 2008, Apple did a ____ job of keeping the iPhone a secret.": "remarkable",
      "So, they ____ with a tire manufacturer in Singapore.": "collaborated",
      "New smartphones are often ____ bigger than the old ones but not much bigger.": "slightly",
      "A portrait is a very ____ type of painting or photo.": "formal",
      "In addition, the brain can process images very quickly and ____ a large number of them as memories.": "store",
      "Most university students will begin to ____ their futures as they near graduation.": "contemplate",
      "It is likely that the move from ownership to usership will continue to expand, partly in response to growing consumer ____ for greener products.": "demand",
      "Unfortunately, shoes ____ quickly if you use them every day.": "wear out",
      "A ____ of Japanese adults asked about their support for a circular economy.": "survey",
      "If you miss too many classes, there will be a ____ in your knowledge.": "gap",
      "Gutiérrez gives a lot of ____ to the people who have worked with him, stressing the importance of having a great team, where each team member has different knowledge and skills.": "credit",
      "For IKEA, usership strategies are helping the company to plan ahead for the day when it will be harder to get the non-____ raw materials needed to produce furniture.": "renewable",
      "Sriram considered a number of different opportunities, but one in particular ____ her — how to feed the world’s growing population.": "appealed to",
      "Family and friends are often very ____ when you lose your job.": "supportive",
      "Realizing that this was an opportunity, he decided to leave his ____ job in the United States and start a ride-hailing service in Colombia, which uses an app, much like Uber.": "secure",
      "She had a ____ mentor, who told her that although many people have excellent ideas, successful entrepreneurs are able to match those ideas to a gap in the market — to something that is missing but the public will want.": "supportive",
      "Most people cannot ____ events from before they were three years old.": "recall",
      "Some of these images were exactly the same as the previous images, and some were ____ different.": "slightly",
      "____ people usually make good salespeople because they are aren't afraid to speak up.": "Assertive",
      "The company was unable to deliver the product on time because of a major ____.": "setback",
      "Instead of photographing the animals in their habitats, he began taking their ____.": "portraits",
      "We can ____ fruits in cans and jars and eat them at a later time.": "preserve",
      "Old family photographs can often ____ memories of childhood.": "trigger",
      "National Geographic photographer Lynn Johnson says that images are so powerful that \"they can change the ____ of people's lives; they can change the ____ of history.“": "course / course",
      "Solar and wind power are ____ sources of energy. Oil and gas are not.": "renewable",
      "The growing population has led to an increased ____ for electricity.": "demand",
      "Many people bring their own bags to a store to carry their ____.": "purchases",
      "On weekends, the streets ____ into a market for street vendors.": "transform",
      "I think your project is too ____. There is a lot to finish before the deadline.": "ambitious",
      "A basic ____ of the circular economy is that any waste should be used to make another product.": "principle",
      "If you close a water bottle ____, the water will not spill out.": "tightly",
      "The Picture Superiority Effect has wide ____.": "implications",
      "Energy companies ____ raw materials from the earth to make oil and steel.": "extract",
      "There is usually a ____ on the number of people allowed in the restaurant.": "limitation",
      "Regular repairs can ____ the life span of appliances for many more years.": "extend",
      "Photos of rare animals from ____ areas of the world in their natural ____ are also very popular.": "remote / habitat",
      "One wealthy ____ gave 10 million dollars to help start the company.": "investor",
      "Smartphones should make our lives easier, but ____, they often do the opposite.": "ironically",
      "When the tires ____, their rubber is recycled as shoe soles, reducing Timberland's need for new rubber.": "wear out",
      "A good boss gives his or her employees a lot of ____ for a successful year.": "credit",
      "Some people think that parking a car in a parking garage is more ____ than parking on the street.": "secure",
      "Before celluloid film, photographers had used glass plates, which were heavy, expensive, and ____.": "fragile",
      "This jewelry might ____ you because it is made in a sustainable way.": "appeal to",
      "A few years later, a British inventor ____ Appert’s method for cans and within a few decades, people could buy cans of soup, juice, vegetables, and meat.": "adapted",
      "Items made of glass are usually ____, so you need to handle them carefully.": "fragile",
      "This waste is an economic design ____.": "flaw",
      "Often, the main ____ of a plan is that it is too expensive.": "drawback",
      "If you’re just sitting there and ____, things will never move forward.": "contemplating",
      "This difference between how the brain responds to images and written text is called the Picture Superiority Effect (see Figure 1), and it explains why our memory of images is ____ to our memory of text.": "superior",
      "Having a reliable food source also allowed them to ____ in one place.": "settle",
      "Unfortunately, most plant-based meats do not have the same taste or ____ as real meat, nor do they provide the same nutrition.": "texture",
      "It is difficult to grow crops in areas where water is ____.": "scarce",
      "He is now working on a second company, an ____ project that Gutiérrez hopes will provide mobile financial services to 400 million people across Latin America who do not have bank accounts.": "ambitious",
      "The ____ to active reading is taking good notes.": "key",
      "A circular economy is based on the idea of ____ waste by keeping raw materials and products in use for much longer.": "eliminating",
      "This placed serious ____ on how they could live.": "limitations",
      "Joel Sartore began his career photographing endangered species in their natural ____.": "habitat",
      "In addition, as a result of pollution, wild seafood often contains plastic and ____ chemicals.": "harmful",
      "Sartore says we should not \"think that we can destroy one species . . . after another and not affect ____.": "humanity",
      "Some companies rely almost ____ on photographs in their advertisements.": "exclusively",
      "New shoes are often ____ and uncomfortable.": "stiff",
      "The ____ of a laptop, which are made in different countries, are put together in a factory.": "components",
      "Car owners are often required to buy ____ in case of a car accident.": "insurance",
      "This effect was ____ in a study in which participants were able to ____ only 10 percent of words presented alone.": "demonstrated / recall",
      "He believes that having a ____ of people who can support you and give you advice is also essential.": "network",
      "The team ____ to create the highest quality product. Each member did a great job.": "collaborated",
      "Most salespeople ____ a goal of increasing their sales every year.": "pursue",
      "Some tree branches don't break even in strong wind because they are ____.": "flexible",
      "Europeans began to cross the ocean and ____ in North America in the mid-16th century.": "settle",
      "Oxygen and water are both ____ for human life.": "critical",
      "A recent ____ of 15,000 people in Asia, Europe, and North America found that consumers are increasingly concerned about the environmental damage that waste can cause.": "survey",
      "They were made from natural raw materials, such as silver and ivory, which were ____ and expensive.": "scarce",
      "Fruits and vegetables are more ____ than cakes and candy.": "nutritious",
      "Electrical ____ were covered in order to prevent these fires.": "wires",
      "Which types of photos are most likely to ____ visitors": "engage",
      "____, one of plastic’s greatest advantages — it doesn’t rust or break down like metal or wood — became its greatest ____.": "Ironically / drawback",
      "The cost of home ____ is increasing every year.": "ownership",
      "The rough ____ of a wool scarf can scratch your skin.": "texture",
      "Working 16 hours a day, 6 days a week is not a ____ way to live.": "sustainable",
      "The process of finding new drugs and other therapies had too many ____ and seemed to take forever, so she began to look for other ways to use her creative energy.": "setbacks",
      "Factories need to have a dependable supply of raw materials.": "supply",
      "The ____ to the success of these usership models is that products are of high quality and can be repaired or ____ easily.": "key / upgraded",
      "Sartore does this ____, knowing that when animals look human, they are likely to ____ a powerful emotional response.": "deliberately / prompt",
      "The circular economy brings us back into ____ with nature.": "harmony",
      "It is more likely than text to ____ an emotional response, and strong emotions are more likely to lead to action or changes in behavior.": "trigger",
      "Environmentalists and economists warn that this model is not ____.": "sustainable",
      "The disappearance of an insect or a frog may not seem very important, but every animal is an important ____ in nature.": "link",
      "It’s hard to imagine a world without food that has been ____ in some way.": "preserved",
      "A lot of people ____ to the latest smartphone as soon as it's released.": "upgrade",
      "Experiments ____ that adult brains are very different from children's brains.": "demonstrate",
      "If you are ____, you will succeed in spite of any problems.": "determined",
      "He traveled to ____ communities in Myanmar, Uganda, and India.": "remote",
      "Some critics of the circular economy ____ it will hurt businesses, but supporters argue the opposite.": "claim",
      "Valuable ____ are ____ from products for reuse in a new or different product.": "components / extracted",
      "Millions of ____ live on our skin and inside our bodies. Most are harmless.": "bacteria",
      "Many of them ____ options like IKEA, the Swedish furniture company, for these ____.": "turn to / purchases",
      "Photographers catch a moment that they hope will ____ their viewers.": "engage",
      "The food you eat begins to ____ as soon as you put it in your mouth.": "break down",
      "Text messaging ____ in the late 1990s and is still popular today.": "took off"
    };
  
    const sentenceArray = Object.entries(sentenceDict);
    setSentences(sentenceArray);
  }, []);

  const shuffleArray = useCallback((array) => {
    for (let i = array.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]];
    }
    return array;
  }, []);

  const generateOptions = useCallback((correctAnswer) => {
    const options = new Set();
    options.add(correctAnswer);
    while (options.size < 6) {
      const randomOption = sentences[Math.floor(Math.random() * sentences.length)][1];
      options.add(randomOption);
    }
    return shuffleArray(Array.from(options));
  }, [sentences, shuffleArray]);

  useEffect(() => {
    if (sentences.length > 0 && isConfigured) {
      const shuffledSentences = shuffleArray(sentences).slice(0, numSentences);
      setSelectedSentences(shuffledSentences);
      setCurrentIndex(0);
      setOptions(generateOptions(shuffledSentences[0][1]));
    }
  }, [numSentences, sentences, isConfigured, shuffleArray, generateOptions]);

  const checkGuess = useCallback((option) => {
    const currentSentence = selectedSentences[currentIndex];
    if (option === currentSentence[1]) {
      setCorrectCount(correctCount + 1);
      if (currentIndex + 1 < numSentences) {
        setCurrentIndex(currentIndex + 1);
        setOptions(generateOptions(selectedSentences[currentIndex + 1][1]));
      } else {
        setCurrentIndex(numSentences);
      }
    } else {
      if (!incorrectSentences.some(sentence => sentence[0] === currentSentence[0])) {
        setIncorrectSentences([...incorrectSentences, currentSentence]);
      }
      setCorrectAnswer(currentSentence[1]);
      setShowOverlay(true);
    }
  }, [currentIndex, incorrectSentences, numSentences, selectedSentences, correctCount, generateOptions]);

  const closeOverlay = useCallback(() => {
    setShowOverlay(false);
    if (currentIndex + 1 < numSentences) {
      setCurrentIndex(currentIndex + 1);
      setOptions(generateOptions(selectedSentences[currentIndex + 1][1]));
    } else {
      setCurrentIndex(numSentences);
    }
  }, [currentIndex, numSentences, selectedSentences, generateOptions]);

  const handleNumSentencesChange = (event) => {
    setNumSentences(parseInt(event.target.value, 10));
  };

  const startGame = () => {
    setSelectedSentences([]);
    setCurrentIndex(0);
    setCorrectCount(0);
    setShowOverlay(false);
    setCorrectAnswer('');
    setIncorrectSentences([]);
    setIsConfigured(false);
  };

  const calculateAccuracy = () => {
    return ((correctCount / numSentences) * 100).toFixed(2);
  };

  if (!isConfigured) {
    return (
      <div className="WordGame">
        <div className="settings">
          <h1>Task 2 설정</h1>
          <label htmlFor="numSentences">문장 개수: </label>
          <input
            type="number"
            id="numSentences"
            min="1"
            max={sentences.length}
            value={numSentences}
            onChange={handleNumSentencesChange}
          />
          <input
            type="range"
            min="1"
            max={sentences.length}
            value={numSentences}
            onChange={handleNumSentencesChange}
          />
          <button onClick={() => setIsConfigured(true)}>시작</button>
        </div>
      </div>
    );
  }

  if (currentIndex >= selectedSentences.length) {
    return (
      <div className="game-over">
        <h1>Task 2 종료</h1>
        {incorrectSentences.length > 0 && (
          <>
            <h2>틀린 문장</h2>
            <ul>
              {incorrectSentences.map((sentence, index) => (
                <li key={index}>
                  <b>{sentence[0]}</b>: {sentence[1]}
                </li>
              ))}
            </ul>
          </>
        )}
        <h2>정답률: {calculateAccuracy()}%</h2>
        <div className="button-container">
          <button onClick={startGame}>다시 시작</button>
          <button className="task-button" onClick={returnToSettings}>Task</button>
        </div>
      </div>
    );
  }

  const progress = (currentIndex / numSentences) * 100;

  return (
    <div className="WordGame">
      <div className="progress-container">
        <p className="progress-text">
          문제 {currentIndex + 1} / {numSentences}
        </p>
        <div className="progress-bar">
          <div className="progress" style={{ width: `${progress}%` }}></div>
        </div>
      </div>
      <div className="word-box">
        <p className="meaning">{selectedSentences[currentIndex][0]}</p>
      </div>
      <div className="options-box">
        <div className="options-container">
          {options.map((option, index) => (
            <button key={index} className="option-button" onClick={() => checkGuess(option)}>{option}</button>
          ))}
        </div>
      </div>
      <p className="score">정답 비율: {correctCount} / {currentIndex}</p>
      {showOverlay && (
        <div className="overlay">
          <div className="overlay-content">
            <p>{correctAnswer}</p>
            <button onClick={closeOverlay}>다음 문장</button>
          </div>
        </div>
      )}
    </div>
  );
}

export default SentenceGame2;
