import React, { useState } from 'react';
import './App.css';
import WordGame from './components/WordGame';
import SentenceGame from './components/SentenceGame';
import SentenceGame2 from './components/SentenceGame2';
import QuoteGame from './components/QuoteGame';

function App() {
  const [selectedGame, setSelectedGame] = useState(null);

  const startGame = (game) => {
    setSelectedGame(game);
  };

  const returnToSettings = () => {
    setSelectedGame(null);
  };

  const renderSelectedGame = () => {
    switch (selectedGame) {
      case 'wordGame':
        return <WordGame returnToSettings={returnToSettings} />;
      case 'sentenceGame':
        return <SentenceGame returnToSettings={returnToSettings} />;
      case 'sentenceGame2':
        return <SentenceGame2 returnToSettings={returnToSettings} />;
      case 'quoteGame':
        return <QuoteGame returnToSettings={returnToSettings} />;
      default:
        return null;
    }
  };

  return (
    <div className="App">
      {selectedGame ? (
        renderSelectedGame()
      ) : (
        <div className="game-selection-container">
          <header className="game-selection">
            <h1>Task 선택</h1>
            <button onClick={() => startGame('wordGame')}>Task 1 : 단어</button>
            <button onClick={() => startGame('sentenceGame2')}>Task 2 : 보기</button>
            <button onClick={() => startGame('sentenceGame')}>Task 3 : 빈칸 </button>
            <button onClick={() => startGame('quoteGame')}>Task 3 : 명언 </button>
          </header>
        </div>
      )}
    </div>
  );
}

export default App;

