import React, { useState, useEffect, useCallback } from 'react';
import './WordGame.css';

function WordGame({ returnToSettings }) {
  const [words, setWords] = useState([]);
  const [selectedWords, setSelectedWords] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [guess, setGuess] = useState('');
  const [showOverlay, setShowOverlay] = useState(false);
  const [correctAnswer, setCorrectAnswer] = useState('');
  const [numWords, setNumWords] = useState(10);
  const [isConfigured, setIsConfigured] = useState(false);
  const [incorrectWords, setIncorrectWords] = useState([]);
  const [correctCount, setCorrectCount] = useState(0);

  useEffect(() => {
    const wordDict = {
      "형] 능숙한, 유창한": "fluent",
      "명] 운, 압운": "rhyme ",
      "명] (시의) 음보": "meter ",
      "동] 표현하다, 연기[연주]하다; ~한 상태로 만들다, 제공하다": "render ",
      "명] 사상자, 피해자": "casualty ",
      "명] 약, 의학": "medicine ",
      "명] 열정": "passion ",
      "명] 시": "poetry ",
      "명] 운문, 시": "verse ",
      "동] 음미하다[즐기다], 만끽하다": "savor ",
      "동] (~를 ..로) 생각하다; 고정시키다": "peg ",
      "명] 냉소가[조소가], 부정적인 사람": "cynic ",
      "동] 간청하다, 마음을 끌다": "appeal ",
      "줄기 세포": "stem cell ",
      "동] 심사숙고하다, 고려하다, 응시하다": "contemplate ",
      "형] 현재의, 지금의": "current ",
      "명] (공간)틈, (시간)공백, 차이": "gap ",
      "형] 해로운": "harmful ",
      "명] 투자자": "investor ",
      "명] 차질": "setback ",
      "명] 공급, 공급품": "supply ",
      "형] 지지하는, 지원하는": "supportive ",
      "명] 감촉, 구성": "texture ",
      "명] 의약품, 약": "drug ",
      "명] 속어, 은어": "slang ",
      "명] 영양분 섭취": "nutrition ",
      "명] 만두, 경단, 심": "dumpling ",
      "단호히 목적을 추구하다, 한 번 해보다; 나가다, 출발하다; 여행을 떠나다": "go for it",
      "동] 추구하다": "pursue ",
      "명] 사업가": "entrepreneur ",
      "동] 설립하다": "found ",
      "은행 계좌": "bank account",
      "형] 적극적인, 확신에 찬": "assertive ",
      "차량 호출": "ride-hailing",
      "형] 단단한, 확고한": "firm ",
      "비공식 경제": "informal economy",
      "명] 대출, 융자금": "loan ",
      "명] 칭찬, 인정": "credit ",
      "형] 주목할 만한, 놀랄 만한": "remarkable ",
      "동] 급격한 변화를 일으키다; 방해하다": "disrupt ",
      "형] 야망을 가진": "ambitious ",
      "형] 단호한, 확고한": "determined ",
      "명] 보험, 보호 수단": "insurance ",
      "형] 안전한, 안정된": "secure ",
      "명] 인적, 물적 연결망": "network ",
      "명] 해당 기업의 주주 지분": "equity ",
      "명] 빗, 빗질": "comb ",
      "형] 희귀한, 부족한": "scarce ",
      "형] 뻣뻣한, (근육이) 뻐근한": "stiff ",
      "명] 평등을 이루게 하는 것; 평형기; 동점골": "equalizer ",
      "명] 결점, 장애": "drawback ",
      "형] 가짜의, 인조의": "fake ",
      "형] 융통성 있는, 잘 구부러지는": "flexible ",
      "형] 깨지기 쉬운, 허약한": "fragile ",
      "부] 비꼬아서, 반어적으로": "ironically ",
      "형] 내화성의, 불연성의": "fireproof ",
      "형] 방수의": "waterproof ",
      "사치품": "luxury item",
      "급격히 인기를 얻다[유행하다]; 이륙하다": "take off",
      "대중의 인식": "public awareness",
      "동] 녹이 슬다, 부식하다": "rust ",
      "명] 화학 물질": "chemical ",
      "명] 해조, 말, 조류 (alga의 복수형)": "algae ",
      "사탕수수": "sugar cane",
      "동] 보존하다, 보호하다": "preserve ",
      "명] 박테리아, 세균 (bacterium의 복수형)": "bacteria ",
      "분해하다": "take apart ",
      "동] 변질되다, 상하다; 망치다": "spoil ",
      "명] 제한, 제약": "limitation ",
      "동] 변형시키다, 탈바꿈시키다": "transform ",
      "동] (분쟁 등을) 해결하다, 정착하다, 진정시키다": "settle ",
      "형] 영양가가 높은": "nutritious ",
      "형] 중대한; 비판적인": "critical ",
      "명] 조건, 환경, 상태": "condition ",
      "형] 북극의": "arctic ",
      "명] 콩": "bean ",
      "명] 산": "acid ",
      "명] 발효": "fermentation ",
      "부] 꼭, 꽉, 단단히": "tightly ",
      "이동 중에": "on the go",
      "동] 맞추다[조정하다]; 적응하다": "adapt ",
      "명] 대륙": "continent ",
      "명] 요리사": "chef ",
      "명] 병, 단지": "jar ",
      "동결건조": "freeze-drying",
      "무관심의 세계적 확산": "a globalization of indifference",
      "갈등의 문화": "a culture of conflict",
      "우리 자신만 생각하다": "think only of ourselves",
      "형] 실체가 없는, 공허한; 무른, 약한, 대단찮은": "insubstantial ",
      "명] 고통": "suffering ",
      "을 쓸고[싣고] 오다, 떠밀려오다": "wash up",
      "지중해": "the Mediterranean",
      "명] 불꽃놀이": "fireworks ",
      "명] (회계) 총계정원장, 원부, 대장": "ledger ",
      "동] …의 화신이다, …을 전형적으로 보여주다": "personify ",
      "명] (격렬한) 기쁨[흥분]": "rush ",
      "형] 예민한, ~을 열망하는": "keen ",
      "동] 높이다, 고조시키다": "heighten ",
      "동] 불안하게 만들다": "unnerve ",
      "동] 협박하다, 겁을 주다": "intimidate ",
      "동] 저장하다": "store ",
      "명] 방식, 방법, 유형": "mode ",
      "동] 촉발시키다, 작동시키다": "trigger ",
      "전쟁지역": "war zone ",
      "자연재해": "natural disaster ",
      "부] 약간, 조금": "slightly",
      "지문": "written text ",
      "동] 보여주다[발휘하다]": "demonstrate ",
      "동] 기억해 내다, 상기하다, 생각나게 하다": "recall ",
      "예를 들어(=for example)": "e.g.",
      "형] 이중의": "dual ",
      "동] 암호로 바꾸다, 부호화하다": "encode ",
      "동] 모으다, 조립하다": "assemble ",
      "형] 우수한, 우세한, 우월한": "superior ",
      "명] 영향[결과], 의미, 함축, 암시": "implication ",
      "부] 독점적으로, 배타적으로": "exclusively ",
      "동] (웹사이트에 정보·사진 등을) 올리다[게시하다]": "post ",
      "이해하다": "make sense",
      "명] 사진작가": "photographer",
      "동] 사진 찍다": "photograph ",
      "명] (희로애락의) 감정, 정서": "emotion",
      "동] (주의·관심을) 사로잡다[끌다]": "engage ",
      "빨리 진행되는": "fast-paced",
      "형] 가만히 있는, 정지한": "still",
      "형] 멸종 위기에 처한": "endangered ",
      "명] 종": "species",
      "명] 서식지": "habitat ",
      "명] 방주": "ark",
      "명] 초상화, 인물 사진": "portrait ",
      "형] 격식을 차린, 정중한": "formal ",
      "전] …을 배경으로, …와 대조적으로": "against",
      "부] 의도[계획]적으로, 고의로": "deliberately",
      "동] 유도하다, 촉발하다": "prompt ",
      "명] 연결 고리, 관련(성), 관계": "link",
      "명] 인간성, 인류, 인간애": "humanity",
      "동] 납득시키다, 확신시키다, 설득하다": "convince ",
      "형] 먼": "remote",
      "동] 고무[격려]하다, 영감을 주다": "inspire ",
      "명] 추이, 전개, 흐름": "course",
      "형] 희끗희끗한": "grizzled",
      "명] (열대) 우림": "rainforest",
      "소유(권)": "ownership",
      "사용(권)": "usership",
      "비결, 열쇠, 실마리": "key",
      "개선하다": "upgrade",
      "도움을 구하다, 의지[의존]하다; 참고하다": "turn to ",
      "구입, 구매[매입]한 것": "purchase ",
      "연장하다": "extend ",
      "쓰레기 매립지": "landfill",
      "(문제·상황 등에 대해) 고심하다[다루다]": "address ",
      "동향, 경향": "trend",
      "제조하다": "manufacture",
      "이익, 이득": "benefit",
      "유지, 보수": "maintenance",
      "소유(물), 재산": "possession ",
      "재생 가능한": "renewable ",
      "수요": "demand ",
      "조사": "survey ",
      "~에 적응하다": "adapt to ",
      "원료": "raw material ",
      "화석 연료": "fossil fuel",
      "선형의": "linear ",
      "지속가능한": "sustainable ",
      "제안하다": "propose ",
      "대안": "alternative ",
      "원형, 순환의": "circular ",
      "제거하다": "eliminate",
      "새로 꾸미다, 재단장하다": "refurbish ",
      "부품": "part ",
      "협력하다": "collaborate ",
      "(낡아서) 떨어지다": "wear out",
      "바닥, 밑창": "sole",
      "제조자, 제조사[생산 회사]": "manufacturer",
      "전자의": "electronic ",
      "전자장치": "electronics ",
      "기기": "appliance ",
      "거래하다, 사고 팔다, 취급하다; …을 웃돈을 주고 신품과 바꾸다": "trade in ",
      "뽑다, 추출하다": "extract ",
      "부화하다": "hatch ",
      "애벌레의 복수형": "larvae",
      "단백질": "protein ",
      "양어장, 양식장": "fish farm "
  };
    const wordArray = Object.entries(wordDict);
    setWords(wordArray);
  }, []);

  useEffect(() => {
    if (words.length > 0 && isConfigured) {
      const shuffledWords = shuffleArray(words).slice(0, numWords);
      setSelectedWords(shuffledWords);
      setCurrentIndex(0);
    }
  }, [numWords, words, isConfigured]);

  const shuffleArray = (array) => {
    for (let i = array.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]];
    }
    return array;
  };

  const handleGuessChange = (event) => {
    setGuess(event.target.value);
  };

  const checkGuess = useCallback(() => {
    const currentWord = selectedWords[currentIndex];
    if (guess.trim().toLowerCase() === currentWord[1].trim().toLowerCase()) {
      setCorrectCount(correctCount + 1);
      setGuess('');
      if (currentIndex + 1 < numWords) {
        setCurrentIndex(currentIndex + 1);
      } else {
        setCurrentIndex(numWords);
      }
    } else {
      if (!incorrectWords.some(word => word[0] === currentWord[0])) {
        setIncorrectWords([...incorrectWords, currentWord]);
      }
      setCorrectAnswer(currentWord[1]);
      setShowOverlay(true);
    }
  }, [guess, currentIndex, incorrectWords, numWords, selectedWords, correctCount]);

  const closeOverlay = useCallback(() => {
    setShowOverlay(false);
    setGuess('');
    if (currentIndex + 1 < numWords) {
      setCurrentIndex(currentIndex + 1);
    } else {
      setCurrentIndex(numWords);
    }
  }, [currentIndex, numWords]);

  useEffect(() => {
    const handleKeyPress = (event) => {
      if (event.key === 'Enter') {
        if (!isConfigured) {
          setIsConfigured(true);
        } else if (showOverlay) {
          closeOverlay();
        } else if (currentIndex >= selectedWords.length) {
          startGame();
        } else {
          checkGuess();
        }
      }
    };

    window.addEventListener('keypress', handleKeyPress);
    return () => {
      window.removeEventListener('keypress', handleKeyPress);
    };
  }, [showOverlay, closeOverlay, checkGuess, currentIndex, selectedWords.length, isConfigured]);

  const handleNumWordsChange = (event) => {
    setNumWords(parseInt(event.target.value, 10));
  };

  const startGame = () => {
    setSelectedWords([]);
    setCurrentIndex(0);
    setGuess('');
    setCorrectCount(0);
    setShowOverlay(false);
    setCorrectAnswer('');
    setIncorrectWords([]);
    setIsConfigured(false);
  };

  const calculateAccuracy = () => {
    return ((correctCount / numWords) * 100).toFixed(2);
  };

  if (!isConfigured) {
    return (
      <div className="WordGame">
        <div className="settings">
          <h1>Task 1 설정</h1>
          <label htmlFor="numWords">단어 개수: </label>
          <input
            type="number"
            id="numWords"
            min="1"
            max={words.length}
            value={numWords}
            onChange={handleNumWordsChange}
          />
          <input
            type="range"
            min="1"
            max={words.length}
            value={numWords}
            onChange={handleNumWordsChange}
          />
          <button onClick={() => setIsConfigured(true)}>시작</button>
        </div>
      </div>
    );
  }

  if (currentIndex >= selectedWords.length) {
    return (
      <div className="game-over">
        <h1>Task 1 종료</h1>
        {incorrectWords.length > 0 && (
          <>
            <h2>틀린 단어</h2>
            <ul>
              {incorrectWords.map((word, index) => (
                <li key={index}>
                  <b>{word[0]}</b>: {word[1]}
                </li>
              ))}
            </ul>
          </>
        )}
        <h2>정답률: {calculateAccuracy()}%</h2>
        <div className="button-container">
          <button onClick={startGame}>다시 시작</button>
          <button className="task-button" onClick={returnToSettings}>Task</button>
        </div>
      </div>
    );
  }

  const progress = (currentIndex / numWords) * 100;

  return (
    <div className="WordGame">
      <div className="progress-container">
        <p className="progress-text">
          문제 {currentIndex} / {numWords}
        </p>
        <div className="progress-bar">
          <div className="progress" style={{ width: `${progress}%` }}></div>
        </div>
      </div>
      <div className="word-box">
        <p className="meaning">{selectedWords[currentIndex][0]}</p>
      </div>
      <input
        type="text"
        value={guess}
        onChange={handleGuessChange}
        placeholder="정답을 입력하세요"
      />
      <button onClick={checkGuess}>정답 제출</button>
      <p className="score">정답 비율: {correctCount} / {currentIndex}</p>
      {showOverlay && (
        <div className="overlay">
          <div className="overlay-content">
            <p>{correctAnswer}</p>
            <button onClick={closeOverlay}>다음 단어</button>
          </div>
        </div>
      )}
    </div>
  );
}

export default WordGame;
